/* eslint-disable jsx-a11y/control-has-associated-label */
import { useLocation, useNavigate } from 'react-router-dom';
import 'assets/css/remoteController.css';
import gridData from 'app/data/deviceStatus/stationStatus/detail/remoteController/gridData';

function RemoteControl() {
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;

  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content remote__control">
      {/* sub nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">{param.deviceName}</dt>
            <dd>{param.groupName}</dd>
          </dl>
        </div>
      </div>
      {/* // sub nav */}
      <h3 className="f__medium section__title">원격 제어</h3>
      {/* 시스템 제어 */}
      <section className="system__control">
        <div className="section__inner">
          <div className="top line">
            <p className="f__medium normal__title">시스템 제어</p>
            <div className="btn__wrap">
              <button className="custom__btn">
                <span className="f__medium">전원차단</span>
              </button>
              <button className="custom__btn">
                <span className="f__medium">부팅</span>
              </button>
            </div>
          </div>
          <div className="btn__wrap">
            <ul>
              <li>
                <button className="custom__btn">
                  <span className="f__medium">SW재시작</span>
                </button>
              </li>
              <li>
                <button className="custom__btn">
                  <span className="f__medium">시스템 재시작</span>
                </button>
              </li>
              <li>
                <button className="custom__btn">
                  <span className="f__medium">시스템 종료</span>
                </button>
              </li>
              <li>
                <button className="custom__btn">
                  <span className="f__medium">스크린 캡쳐</span>
                </button>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 시스템 제어 */}
      {/* 스테이션 제어 */}
      <section className="station__control">
        <div className="section__inner">
          <div className="top line">
            <p className="f__medium normal__title">스테이션 제어</p>
            <div className="btn__wrap">
              <button className="custom__btn">
                <span className="f__medium">서비스 중지</span>
              </button>
              <button className="custom__btn">
                <span className="f__medium">서비스 정상화</span>
              </button>
            </div>
          </div>
          <div className="common__grid">
            <table>
              <thead>
                <tr>
                  <th />
                  <th />
                  <th />
                  <th>
                    <button className="custom__btn">
                      <span className="f__medium">전체 중지</span>
                    </button>
                  </th>
                  <th>
                    <button className="custom__btn">
                      <span className="f__medium">전체 재개</span>
                    </button>
                  </th>
                  <th />
                  <th>
                    <button className="custom__btn">
                      <span className="f__medium">전체 열기</span>
                    </button>
                  </th>
                  <th>
                    <button className="custom__btn">
                      <span className="f__medium">전체 닫기</span>
                    </button>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <th>
                    <p className="f__medium">슬롯 번호</p>
                  </th>
                  <th>
                    <p className="f__medium">배터리 ID</p>
                  </th>
                  <th>
                    <p className="f__medium">충전 상태</p>
                  </th>
                  <th>
                    <p className="f__medium">충전 중지</p>
                  </th>
                  <th>
                    <p className="f__medium">충전 재개</p>
                  </th>
                  <th>
                    <p className="f__medium">도어 상태</p>
                  </th>
                  <th>
                    <p className="f__medium">도어 열기</p>
                  </th>
                  <th>
                    <p className="f__medium">도어 닫기</p>
                  </th>
                </tr>
                {gridData.map((obj, idx) => (
                  <tr key={obj.id}>
                    <th>
                      <p className="f__medium">{obj.no}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.id}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.chargeState}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.stop}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.restart}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.doorState}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.doorOpen}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.doorClose}</p>
                    </th>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </section>
      {/* // 시스템 제어 */}
    </div>
  );
}

export default RemoteControl;
